import React, { useEffect, useState } from 'react';
import Form from './components/Form';
import Api from './Api';

const App = () => {
    const[states, setStates] = useState([]);
    const[districts, setDistricts] = useState([]);
    const[currentDate, setNewDate] = useState(null);
    const[districtId, setDistrictId] = useState(0);
    
    const[centers, setCenters] = useState([]);
    const onFormSubmit = (e) => {
        e.preventDefault();
        const pageData = async () => {
            const {data} = await Api.get(`/appointment/sessions/public/calendarByDistrict?district_id=${districtId}&date=${currentDate}`);
            setCenters(data.centers);
        }
        pageData();
    }
    
    useEffect(() =>{
        const stateData = async () => {
            const {data} = await Api.get('/admin/location/states');
            setStates(data.states);
        }
        stateData();
    }, []);

    const getSelectedState = (e) => {
        const stateId = e.target.value;
        const districtData = async () => {
            const {data} = await Api.get(`/admin/location/districts/${stateId}`);
            setDistricts(data.districts);
        }
        districtData();
    }
    const onDistrictChange = (e) => {
        setDistrictId(e.target.value);
    }

    const onDateChange = (event, data) => {
        setNewDate(data.value);
        let date = new Date(data.value);
        let dateDMY = `${date.getDate()}-${date.getMonth() + 1}-${date.getFullYear()}`;
        setNewDate(dateDMY);
    }
    
    
    return (
        <div className="ui grid container">
            <div className="row">
                <div className="column">
                    <div className="ui message">
                        <h1 className="ui header">Covid Vaccine Center</h1>
                        <p>This is the information about covid vaccine center availability</p>
                        <a target="_blank" href="https://selfregistration.cowin.gov.in/" className="ui blue">Get more details on official page</a>
                    </div>
                </div>
            </div>
            <div className="row">
                <div className="column">
                    <div className="ui segment">
                        <Form 
                            onFormSubmit={onFormSubmit}
                            states={states}
                            onStateChange={getSelectedState}
                            districts={districts}
                            onDistrictChange={onDistrictChange}
                            onDateChange={onDateChange}
                        /> 
                    </div>
                </div>
            </div>
            <div className="row">
                <div className="column">
                    <div className="ui segment">
                        <div className="ui relaxed divided list">
                            {(!centers.length > 0) ? <div>Data not present, please select the required field and submit.</div>
                                :
                                centers.map(center =>
                                    <div key={center.center_id} className="item">
                                        <i className="map marker icon"></i>
                                        <div className="content">
                                            <div className="header">{center.name} PIN Code : {center.pincode}</div>
                                            <div className="description">{center.sessions[0].available_capacity} vaccine available at {center.block_name}</div>
                                        </div>
                                    </div>
                                )
                            }
                        </div>
                    </div> 
                </div>
            </div>
               
        </div>
    );
}

export default App;